@charset "UTF-8";
body {
  margin: 0;
  font-family: 'Comfortaa'; }

.component {
  background: #eee;
  height: 100vh;
  /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  height: calc(var(--vh, 1vh) * 100); }

.template {
  height: calc(100% - 100px);
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  border: 1px solid black;
  background: #fff; }

.actions {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center; }

.dot {
  height: 20px;
  width: 20px;
  background: #000;
  border-radius: 50%;
  will-change: transform;
  transition: .65s transform ease;
  position: absolute; }

.device-info {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #eff8fa;
  text-align: center;
  display: none;
  align-items: center;
  padding: 0 5px; }

@media screen and (orientation: portrait) {
  .body {
    overflow: hidden; }
  .device-info {
    display: flex;
    justify-content: center; }
  .component {
    display: none; } }
